@import "sass/index";

.input {
  height: 40px;
  width: 100%;

  background: white;
  border-radius: 4px;
  border: none;

  padding: 12px;
  margin: 0;

  font-size: 1rem;
  font-family: "Gilroy";
  outline: none;

  transition: box-shadow 0.3s;

  @media (max-width: 360px) {
    font-size: 0.875rem;
  }
  &:focus {
    box-shadow: 0px 0px 3px 2px use(--secondary-main);
  }
}
