.Roller__Box {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Unlock, "Gilroy", Arial, serif;
}

.DigitRoll {
  font-weight: 400;
  margin-top: -25%;

  font-size: 90px;
  color: #00ffc3;
  border: none !important;

  transition: color 1s;
  position: relative;
}

.DigitRoll::after,
.DigitRoll::before {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  left: 0;
  z-index: 3;
}

.DigitRoll::after {
  top: -10px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, transparent);

  height: 60px;
}

.DigitRoll::before {
  bottom: -10px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 10%, transparent);
  height: 90px;
}

.DigitRoll__Cell {
  margin: 0;
  position: relative;

  z-index: 0;
  filter: contrast(1.2);
}

.DigitRoll__Divider {
  display: none !important;
}

.DigitRoll__Divider {
  font-size: 90px;
  color: transparent;
  padding: 0;
}

.autoOn {
  transition: color 0.4s;
}

.textFail {
  color: #dd5389;
}

.Roller__afterNumber {
  position: absolute;
  top: 5.5rem;
}
.Roller__beforeNumber {
  position: absolute;
  bottom: 5.5rem;
}
