@import "sass/index";

.root {
  position: relative;
}
.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 15px 0 5px;

  @media (max-width: 360px) {
    font-size: 0.685rem;
  }
}
.betsCountInput {
  height: 40px;
  border-radius: spacing(0.5);
  & input {
    transition: all 300ms ease-out 0s;
  }

  @media (max-width: 360px) {
    font-size: 0.875rem;
  }
}
.betsCountInputPadding {
  & input {
    padding-right: spacing(5);
  }
}
.inputWrapper {
  position: relative;

  & :first-child {
    margin-top: 0;
  }
}
.infinityIcon {
  position: absolute;
  right: spacing(1);
  top: spacing(1);
  width: 24px;
  height: 24px;
}
