$boxGap: 40px;

.decisionsBox {
  display: flex;
  justify-content: space-between;
  gap: $boxGap;

  @media (max-width: 475px) {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0;
    margin: 20px auto;
  }
}
.bottomBox {
  display: flex;
  justify-content: space-between;
  gap: $boxGap;

  & > * {
    width: 100%;
    max-width: 320px;
  }
  @media (max-width: 435px) {
    flex-direction: column;
    justify-content: center;
    gap: 0;
    align-items: center;
  }
}
