@import "sass/index";

.button {
  font-size: 1.125rem;
  font-weight: bold;
  font-family: "Gilroy";
  border-radius: 8px;
  min-width: 200px;
  max-width: 250px;
  width: 100%;
  background: use(--template-gameCell-gameButtons-dice) !important;
  color: use(--primary-main);
  transition: transform 0.5s;
  border: none;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  padding: 6px 16px;

  &hover,
  &active,
  &focus {
    transform: scale(1.05);
  }
  &:disabled {
    opacity: 0.5;
  }

  @media (max-width: 560px) {
    padding: 0;
    font-size: 1rem;
  }
}
.buttonWrapper {
  height: 45px;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.buttonText {
  margin: 0;
  line-height: 100%;
}

.controlBox {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-gap: 10px;
  justify-items: center;
  align-items: center;

  @media (max-width: 560px) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
      "switch_1 switch_2"
      "button button";
  }

  @media (max-width: 400px) {
    grid-template-columns: 1fr;
    grid-template-areas:
      "switch_1"
      "switch_2"
      "button";
  }
}

.switch_1 {
  @media (max-width: 560px) {
    grid-area: switch_1;
  }
}
.switch_2 {
  @media (max-width: 560px) {
    grid-area: switch_2;
  }
}
.buttonArea {
  @media (max-width: 560px) {
    grid-area: button;
  }
}
