@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: use(--secondary-main);
}
.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}
.gameBlock {
  overflow: hidden;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  display: flex;
  box-shadow: 0px 12px 7px rgba(0, 0, 0, 0.1);
}
.statisticsWrapper {
  padding-top: 26px;
}
.gameWrapper {
  width: 100%;
  min-height: 200px;
  height: 100%;
  position: relative;
}
.gameCell {
  background: use(--secondary-light);
  height: 100%;
  width: 100%;
}
.gameFooter {
  padding: 10px;
}
