@import "sass/index";

.box {
  width: min-content;
  height: 100%;
  margin: 0 auto;
}
.betBox {
  margin: 0 auto;
  width: 100%;

  @media (max-width: 1150px) {
    width: 100%;
  }
}
.panelBox {
  position: relative;
}
.imgPanel {
  object-fit: cover;
  width: 100%;
}
.dataWrapper {
  width: 850px;
  height: 530px;
  transform-origin: left top;
}
.textPanel {
  font-family: "Unlock", "Gilroy", san-serif;
  font-weight: 400;
  color: use(--info-light);
  letter-spacing: 15px;
  font-size: 90px;

  position: absolute;
  top: 25%;
  left: 0;
  width: 100%;
  text-align: center;
  margin: 0;
}
.textFail {
  color: use(--subsidiary-red);
}

.range {
  position: absolute;
  width: 100%;
  top: calc(25% + 15px);
  left: 0;

  @media (max-width: 1150px) {
    top: auto;
    bottom: 15%;
    display: flex;
    justify-content: center;
  }

  @media (max-width: 500px) {
    bottom: 10%;
  }

  @media (max-width: 390px) {
    bottom: 0;
  }
}

.rangeBox {
}

.rangeMin {
  text-align: right;
  margin-right: -20px;

  @media (max-width: 1150px) {
    margin-right: 0;
  }
}
.rangeMax {
  text-align: left;
  margin-left: -20px;

  @media (max-width: 1150px) {
    margin-left: 0;
  }
}
.rangeText {
  color: use(--template-modal-title-color);
  font-size: 2rem;
  width: 100%;

  @media (max-width: 500px) {
    font-size: 1.7rem;
  }
}
