@import "sass/index";

.root {
  padding: 0;
  margin: 0;
  border: none;
}

.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 15px 0 5px;

  @media (max-width: 360px) {
    font-size: 0.685rem;
  }
}
.check {
  color: use(--template-modal-title-color);
}
.label {
  margin: 0;
  color: use(--secondary-main);

  @media (max-width: 360px) {
    font-size: 0.875rem;
  }
}

.radio {
  &Label {
    color: use(--secondary-main);
  }
  &Round {
    background-color: use(--template-modal-title-color);
  }
  &Radio {
    border-color: use(--template-modal-title-color);
  }
}

div.radioRoot {
  margin-bottom: 15px;
}
