.box {
  display: grid;
  grid-template-columns: minmax(300px, 1fr) minmax(290px, 1fr);
  gap: 10px;
  grid-gap: 10px;
  align-items: end;

  @media screen and (min-width: 960px) and (max-width: 1020px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 720px) {
    grid-template-columns: 1fr;
  }
}

.elements {
  display: grid;
  gap: 10px;
  grid-gap: 10px;
  grid-template-columns: minmax(150px, 1fr) minmax(140px, 2fr);
  align-items: end;

  @media (max-width: 360px) {
    grid-template-columns: 1fr;
  }
}

.payoutElements {
  display: grid;
  gap: 10px;
  grid-gap: 10px;
  grid-template-columns: minmax(50px, 1fr) minmax(90px, 1fr);
}

.inputWrapper {
  position: relative;
  & :first-child {
    margin: 0;
  }
}

.inputPercent {
  padding-right: 25px !important;
}
.percentWrapper {
  position: relative;
  &::after {
    content: "%";
    font-size: 1rem;
    color: black;
    font-family: "Gilroy";

    position: absolute;
    right: 10px;
    top: 10px;
  }
}
.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 15px 0 5px;

  @media (max-width: 360px) {
    font-size: 0.685rem;
  }
}
