.root {
  margin-top: 15px;
  & > h1 {
    display: inline-block;
    width: 50%;
    margin: 0;
    font-size: 0.875rem;
    text-transform: uppercase;
    padding-bottom: 5px;
    @media (max-width: 360px) {
      font-size: 0.685rem;
    }
  }
  & > h1:nth-child(2) {
    padding-left: 5px;
  }
}
.inputsWrapper {
  display: flex;
  & > input:first-child {
    margin-right: 5px;
  }
  & > input:last-child {
    margin-left: 5px;
  }
}
