@import "sass/index";

.root {
  max-width: 320px;
  padding: 0;
}

.inputWrapper {
  position: relative;

  & :first-child {
    margin: 0;
  }

  @media screen and (min-width: $middleTabletScreen) and (max-width: $fullHDScreen) {
    display: flex;
    justify-content: flex-end;

    & :first-child {
      max-width: 100;
    }
  }

  @media screen and (min-width: 475px) and (max-width: 780px) {
    display: flex;
    justify-content: flex-end;

    & :first-child {
      max-width: 100;
    }
  }
}
.inputUnit {
  margin: 0;

  @media (max-width: $littleScreen) {
    font-size: 0.875rem;
  }
}

.percentWrapper {
  position: relative;
  &::after {
    content: "%";
    position: absolute;
    font-size: 1rem;
    color: black;
    font-family: "Gilroy";

    right: 10px;
    top: 10px;
  }
}

.inputPercent {
  padding-right: 25px !important;
}
.input {
  margin: 0;
  height: 40px;
}
.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 15px 0 5px;

  @media (max-width: $littleScreen) {
    font-size: 0.685rem;
  }
}
.check {
  color: use(--template-modal-title-color);
}
.label {
  margin: 0;
  color: use(--secondary-main);
}
.checkBox {
  color: use(--template-modal-title-color);
  margin: 0;
  margin-left: -1px;
  margin-bottom: 10px;

  & > span {
    padding-left: 10px;
  }
}
.field {
  display: grid;
  grid-template-columns: calc(70% - 10px) 30%;
  gap: 10px;
  grid-gap: 10px;

  @media screen and (min-width: $middleTabletScreen) and (max-width: $fullHDScreen) {
    grid-template-columns: 1fr;
  }

  @media screen and (min-width: 475px) and (max-width: 780px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 340px) {
    grid-template-columns: calc(70% - 10px) 30%;
  }
}
.inputsBox {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  grid-gap: 10px;
}

.radio {
  &Label {
    color: use(--secondary-main);
  }
  &Round {
    background-color: use(--template-modal-title-color);
  }
  &Radio {
    border-color: use(--template-modal-title-color);
  }
}

div.radioRoot {
  margin-bottom: 15px;
}

.pnlOptions {
  padding-left: 0;
  margin: 0;
  border: none;
}
