@import "sass/index";

$buttonMargin: 5px;

.box {
  position: relative;
}
.inputWrapper {
  position: relative;
  & :first-child {
    margin: 0;
  }
}
.buttonsBox {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  align-items: center;

  @media (max-width: 390px) {
    margin-top: $buttonMargin;
    position: relative;
    grid-gap: $buttonMargin;
    gap: $buttonMargin;
  }
}
.button {
  color: white;
  background: use(--secondary-main) !important;
  box-shadow: inset 0px -2px 0px 0px rgba(0, 0, 0, 0.32) !important;
  margin-right: 2px;
  max-width: 40px;
  min-width: 0;
  font-size: 0.75rem;
  padding: 5px;

  @media (max-width: 390px) {
    max-width: 100%;
  }
}
.dollarLabel {
  position: absolute;
  left: 26px;
  top: 0;
  height: 100%;
  font-size: 16px;
  color: #000000;
  display: flex;
  align-items: center;
}
.coinImg {
  left: 9px;
}
